import { Component, Injector } from '@angular/core';
import { AnalyticsInteractor, AnalyticsType } from '@nequi/features/commons/core';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import { NavigatorProvider, TranslateProvider } from '@nequi/features/commons/providers';
import { BottomSheetController, FirstTimeMessageWithSelectOrganism } from '@nequi/ui';
import { FirstTimeMessageBottomSheetService } from '@nequi/ui/services';
import { getFormattedDateTimes } from '@nequi/utils/date';
import { openUrl, stringsArrayToObjectWithValues } from '@nequi/utils/functions';

import { HowToUseMoneyOptions, MessagesBottomSheet } from '../../../core';
import { HowToUseMoneyConfig } from './how-to-use-money.config';

@Component({
  selector: 'nequi-how-to-use-money',
  templateUrl: './how-to-use-money.page.html',
  styleUrls: ['./how-to-use-money.page.scss'],
})
export class HowToUseMoneyPage extends FeatureBasePage {
  public config = HowToUseMoneyConfig;
  constructor(
    injector: Injector,
    public navigatorProvider: NavigatorProvider,
    private firstTimeMessageBottomSheetService: FirstTimeMessageBottomSheetService,
    private translateProvider: TranslateProvider,
    private bottomSheetController: BottomSheetController,
    private analyticsInteractor: AnalyticsInteractor
  ) {
    super(injector);
  }

  public async goBack() {
     this.navigatorProvider.back();
  }

  public onOptionClicked(option) {
    let bottomSheetMessages: MessagesBottomSheet;

    switch (option.id) {
      case HowToUseMoneyOptions.TO_PAY:
        bottomSheetMessages = this.translateProvider.get(
          this.config.options[HowToUseMoneyOptions.TO_PAY].bottomSheet
        );

        this.showBottomSheetFirstMessage(bottomSheetMessages, option.id);
        this.setAnalyticsIntentionOfCardRequest(
          this.config.analytics.events.card.actions.pay,
          this.config.analytics.events.card.name
        );

        break;

      case HowToUseMoneyOptions.TO_BUY:
        bottomSheetMessages = this.translateProvider.get(
          this.config.options[HowToUseMoneyOptions.TO_BUY].bottomSheet
        );

        this.showBottomSheetFirstMessage(bottomSheetMessages, option.id);
        this.setAnalyticsIntentionOfCardRequest(
          this.config.analytics.events.card.actions.buy,
          this.config.analytics.events.card.name
        );

        break;

      case HowToUseMoneyOptions.TRANSPORT:
        bottomSheetMessages = this.translateProvider.get(
          this.config.options[HowToUseMoneyOptions.TRANSPORT].bottomSheet
        );

        this.showBottomSheetFirstMessageWhitSelect(bottomSheetMessages);
        this.setAnalyticsIntentionOfCardRequest(
          this.config.analytics.events.card.actions.transport,
          this.config.analytics.events.card.name
        );

        break;

      case HowToUseMoneyOptions.SERVICES:
        bottomSheetMessages = this.translateProvider.get(
          this.config.options[HowToUseMoneyOptions.SERVICES].bottomSheet
        );

        this.showBottomSheetFirstMessage(bottomSheetMessages, option.id);
        this.setAnalyticsIntentionOfCardRequest(
          this.config.analytics.events.card.actions.services,
          this.config.analytics.events.card.name
        );

        break;

      case HowToUseMoneyOptions.OTHERS:
        bottomSheetMessages = this.translateProvider.get(
          this.config.options[HowToUseMoneyOptions.OTHERS].bottomSheet
        );

        this.showBottomSheetFirstMessage(bottomSheetMessages, option.id);
        this.setAnalyticsIntentionOfCardRequest(
          this.config.analytics.events.card.actions.others,
          this.config.analytics.events.card.name
        );

        break;

      default:
        this.navigatorProvider.navigateForward(this.config.urls.others);
        break;
    }
  }

  private async showBottomSheetFirstMessage(
    data: MessagesBottomSheet,
    option: HowToUseMoneyOptions
  ) {
    await this.firstTimeMessageBottomSheetService.show({
      icon: data.icon,
      message: data.title,
      content: data.description,
      button: {
        label: data.firstButton,
        color: 'guayaba',
        fill: 'outline',
        action: async () => {
          this.firstOptionSelectedBottomSheet(option, data.url);
        },
      },
      buttonNext: {
        label: data.secondButton,
        color: 'medianoche',
        fill: 'outline',
        action: async () => {
          this.firstTimeMessageBottomSheetService.hide(true);
          this.skipFirsOptionSelectedBottomSheet(option);
        },
      },
    });
  }

  private firstOptionSelectedBottomSheet(option: HowToUseMoneyOptions, url: string) {
    switch (option) {
      case HowToUseMoneyOptions.TO_PAY:
        this.navigatorProvider.navigateForward(url);

        break;

      case HowToUseMoneyOptions.TO_BUY:
        openUrl(url);
        break;

      case HowToUseMoneyOptions.SERVICES:
        this.setAnalyticsIntentionOfCardRequest(
          this.config.analytics.events.payServives.actions.payServices,
          this.config.analytics.events.payServives.name
        );

        this.navigatorProvider.navigateForward(url, { state: { id: 'publicServices' } });
        break;

      case HowToUseMoneyOptions.OTHERS:
        this.firstTimeMessageBottomSheetService.hide(true);
        this.navigatorProvider.navigateForward(url);
        break;

      default:
        break;
    }
  }

  private skipFirsOptionSelectedBottomSheet(option: HowToUseMoneyOptions) {
    switch (option) {
      case HowToUseMoneyOptions.TO_PAY:
        this.navigatorProvider.navigateForward(this.config.urls.others);
        this.setAnalyticsIntentionOfCardRequest(
          this.config.analytics.events.skipPay.actions.skip,
          this.config.analytics.events.skipPay.name
        );

        break;

      case HowToUseMoneyOptions.TO_BUY:
        this.navigatorProvider.navigateForward(this.config.urls.others);
        break;

      case HowToUseMoneyOptions.TRANSPORT:
        this.navigatorProvider.navigateForward(this.config.urls.others);
        this.setAnalyticsIntentionOfCardRequest(
          this.config.analytics.events.skipTransport.actions.skip /*  */,
          this.config.analytics.events.skipTransport.name
        );

        break;

      case HowToUseMoneyOptions.SERVICES:
        this.navigatorProvider.navigateForward(this.config.urls.others);
        this.setAnalyticsIntentionOfCardRequest(
          this.config.analytics.events.skipServices.actions.skip,
          this.config.analytics.events.skipServices.name
        );

        break;

      case HowToUseMoneyOptions.OTHERS:
        this.navigatorProvider.navigateForward(this.config.urls.others);
        this.setAnalyticsIntentionOfCardRequest(
          this.config.analytics.events.skipNequiCard.actions.skip,
          this.config.analytics.events.skipNequiCard.name
        );

        break;

      default:
        break;
    }
  }

  private async showBottomSheetFirstMessageWhitSelect(data: MessagesBottomSheet) {
    const bottomSheet = this.bottomSheetController.create({
      component: FirstTimeMessageWithSelectOrganism,
      componentProps: {
        icon: data.icon,
        message: data.title,
        content: data.description,
        options: this.config.cities,
        label: data.label,
        button: {
          label: data.firstButton,
          color: 'medianoche',
          fill: 'outline',
          action: async () => {
            this.firstTimeMessageBottomSheetService.hide(true);
            this.navigatorProvider.navigateForward(this.config.urls.others);
          },
        },
        optionSelected: async (option) => {
          if (option.code === this.config.medellinCode) {
            this.navigatorProvider.navigateForward(this.config.urls.civica);
          } else {
            this.navigatorProvider.navigateForward(this.config.urls.others);
          }
        },
      },
    });

    await bottomSheet.open();
  }

  private async setAnalyticsIntentionOfCardRequest(event, eventName) {
    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: { eventName: eventName },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: eventName },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: eventName,
        eventProps: stringsArrayToObjectWithValues(
          this.config.analytics.events.card.params.cleverTap,
          [event, getFormattedDateTimes()]
        ),
      },
    });
  }
}
